.containerCard {
  border-radius: 10px;
  background-color: #10303e;
  height: 100%;
  min-height: 280px;
  position: relative;
  width: 100%;
  /* background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 0, 0, 0.5) 100%
  ); */
}
.cardImg {
  border-radius: 10px;
  display: flex;
  width: 100%;
  height: 70%;
  margin: auto;

}
.darken{
  background-color: #10303e;
  border-radius: 10px;
  height: 100%;
  left: 0;
  opacity: 0.2;
  position: absolute;
  top: 0;
  width: 100%;
}
.cardTitre {
  bottom: 20px;
  color: #ffffffc2;
  font-size: 1.125em;
  left: 20px;
  margin-right: 20px;
  position: absolute;
}

@media screen and (max-width: 768px) {
  .containerCard {
    border-radius: 10px;
    height: 100%;
    min-height: 255px;
    position: relative;
    width: 100%;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0) 0%,
      rgba(0, 0, 0, 0.5) 100%
    );
  }
  .cardImg {
    border-radius: 10px;
    height: 100%;
    object-fit: cover;
    width: 100%;
  }
}
