.carousel-container {
    position: relative;
    width: 100%;
    overflow: hidden;
    border-radius: 25px;
    height: 100%;
    height: 415px;
  }
  
  .carousel-image {
    height: 415px;
    width: 100%;
    object-fit: cover;
    /* border: solid 3px #10303e;
    border-radius: 25px; */
  }
  
  .arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    width: 50px;
  }
  
  .left-arrow {
    left: 20px;
  }
  
  .right-arrow {
    right: 20px;
  }
  .pagination {
    position: absolute;
    bottom: 20px;
    left: 50%;
    font-size: 18px;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.01);
    color: #10303e;
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  @media screen  and (max-width: 768px) {
    .arrow {
      width: 24px;
      height: 35px;
    }
    .carousel-container {
      position: relative;
      width: 100%;
      overflow: hidden;
      border-radius: 25px;
      height: 100%;
      height: 255px;
    }
  
    .carousel-image {
      width: 100%;
      height: 255px;
    }
  
    .pagination {
      font-size: 14px;
    }
  }
  